<template>
  <form style="
      height: 100vh;
      width: 100vw;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    "
  >
    <input type="email" placeholder="Email" required v-model="email">
    <input type="password" placeholder="Password" required v-model="pass">

    <button type="button" @click="submit">Login</button> 
  </form>
</template>

<script>
import axios from '../Scripts/service/axios.js';

export default {
  data() {
    let data = {
      email: '',
      pass: ''
    }
    return data;
  },

  methods: {
    submit(event) {
      event.preventDefault()

      let data = {
        'email': this.email,
        'password': this.pass
      }

      axios.post('login/', data).then(res => {
        console.log(res, 'reeeeesssss')
        this.$router.push('home')
      })
      .catch(err => {
        console.error(err)
      })
    }
  }
}
</script>